.wrapper {
  height: 100%;
  overflow-y: auto;
}
.wrapper_personal {
  padding: 10px 15px;
}
.wrapper_personal_header {
  display: flex;
}
.wrapper_personal_header::before {
  content: '';
  width: 4px;
  margin-right: 10px;
  background-color: #FF0000;
}
.wrapper_personal_body {
  margin-top: 10px;
}
.wrapper_personal_body_item {
  border-radius: 6px;
  width: 32%;
}
.wrapper_personal_body_item img {
  border-radius: 6px;
}
